.listeningExercise{
    margin-left: 8%;
    margin-right: 8%;
    margin-top: 2%;
}

.Output-Audio{
    border: 1px solid black;
}

.Listening-gen-que-btn{
    padding: 10px 20px;
    margin-top: 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    box-shadow: 2px 2px 2px rgb(195, 195, 195);
    margin-left: 20%;
}

.Listening-gen-que-btn:hover{
    background-color: #45a049;
}