
.main-speaking-container{
    text-align: center;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.speaking-container {
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    height: 300px; /* Set a fixed height for the container */
    overflow-y: auto; /* Enable vertical scrolling */
    position: relative; /* Enable relative positioning for button */
  }
  
  .message {
    padding: 8px;
    margin-bottom: 8px;
    border-radius: 8px; /* Rounded corners for messages */
    word-wrap: break-word; /* Allow text wrapping within messages */
    clear: both; /* Clear floats */
  }
  
  .user-message {
    background-color: #DCF8C6;
    color: #333;
    float: right; /* Align user messages to the left */
    width: 60%; /* Set width for user messages */
  }
  
  .ai-message {
    background-color: #E6E6E6;
    color: #333;
    float: left; /* Align AI messages to the right */
    width: 60%; /* Set width for AI messages */
  }
  
  .speaking-button-container {
    position: absolute; /* Position button container relative to speaking container */
    bottom: 20px; /* Adjust distance from bottom */
    left: 50%; /* Align button container to the center */
    transform: translateX(-50%); /* Center button container */
    display: flex;
    justify-content: center;
  }
  
  .speaking-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 10px;
    box-shadow: 4px 4px 4px rgb(197, 196, 196);
  }
  
  .speaking-button:hover {
    background-color: #0056b3;
  }
  .chat-container {
    height: calc(100% - 60px); /* Adjust height to leave space for buttons */
    overflow: auto; /* Enable automatic scrolling */
    -ms-overflow-style: none; /* Hide scrollbar in IE and Edge */
    scrollbar-width: none; /* Hide scrollbar in Firefox */
  }
  
  .chat-container::-webkit-scrollbar {
    display: none; /* Hide scrollbar in WebKit browsers */
  }

  .assessment-result-speaking{
    font-size: 16px;
    white-space: pre-wrap;
    text-align: justify;
    margin-left: 18%;
    margin-right: 18%;
    margin-top: 2%;

  }

  .loadingIcon {
    animation: rotation 1s linear infinite;
  }
  
  @keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .assess-speaking-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50; /* Green color */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .restart-conversation-btn{
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff; /* Blue color */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }