/* Writing Component Styles */

/* Container styles */
.writing-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Heading styles */
  .writing-heading {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center; /* Center the heading */
  }
  
  /* Topic styles */
  .topic-container {
    margin-bottom: 20px;
  }
  
  /* Textarea styles */
  .textarea-container {
    margin-bottom: 20px;
  }
  
  .textarea-input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  /* Button styles */
  .button-container {
    text-align: center;
  }
  
  .assess-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50; /* Green color */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .assess-button:hover {
    background-color: #45a049; /* Darker green color on hover */
  }
  
  /* Assessment styles */
  .assessment-container {
    margin-top: 20px;
    animation: fadeIn 0.5s ease; /* Fade-in animation */
  }
  
  .assessment-heading {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .assessment-result {
    font-size: 16px;
    white-space: pre-wrap;
    text-align: justify;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  