.SignUp-nav{
    top: 0%;
    display: flex;
    width: 100%;
    left: 0;
    height: 80px;
    color: rgb(249, 189, 99);
    background-color: rgb(0, 153, 255);
    box-shadow: 8px 8px 8px 1px rgb(206, 200, 200);
}

.SignUp-p{
    text-align: center;
    width: 100%;
    padding: 0%;
    margin: 0%;
    margin-left: 6.4%;
}

.SignUp-Form{
    top: 35px;
    align-items: center;
    position: relative;
    height: 400px;
    width: 260px;
    background-color:antiquewhite;
    margin: auto;
    border-radius: 6px;
    padding: 25px;
}

#name, #email, #password, #dob, #gender{
    margin: 10px;
}

.SignUp-select{
    width: 236px;
    height: 32px;
    border-radius: 4px;
    border-style: solid;
    border-width: 1.5px;
    border-color: black;
    margin: 10px;
}

.SignUp-Btn{
    align-items: center;
    margin: 10px;
    height: 30px;
    width: 100px;
    background-color: rgb(249, 189, 99);
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-width: 1.5px;
    border-style: solid;
    border-radius: 4px;
}

.SignUp-input{
    width: 230px;
    height: 30px;
    border-width: 1.5px;
    border-style:solid;
    border-radius: 4px;
    border-color: black;
}

.SignUp-Google-Button{
    align-items: center;
    margin-top: 10px;
    margin-left: 28%;
    height: 30px;
    width: 100px;
    background-color: rgb(249, 189, 99);
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-width: 1.5px;
    border-style: solid;
    border-radius: 4px;
}

.SignUp-btn{
    height: 30px;
    width: 80px;
    margin-top: 1.8%;
    margin-right: 1%;
    background-color: rgb(249, 189, 99);
    border-width: 0;
    border-radius: 4px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: black;
}

.SignUp-h1{
    margin: 0;
    padding: 0;
}

.SignUp-h3{
    margin: 0;
    padding: 0;
}