/* Styling for the container div */
.mcq-card-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    
  }
  
  /* Styling for the card */
  .card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
    transition: box-shadow 0.3s ease;
    border-left: 5px solid   #1886fd;;
    border-right: 5px solid   #007bff;;
    background-color:rgba(250, 247, 242, 0.664);
  }
  
  .card:hover {
    box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.1);
  }
  
  /* Styling for the question heading */
  .question-heading {
    margin-bottom: 10px;
    font-size: 24px;
    color: #333;
  }
  
  /* Styling for the question text */
  .question-text {
    margin-bottom: 20px;
    font-size: 18px;
    color: #555;
  }
  
  /* Styling for the options list */
  .options-list {
    list-style: none;
    padding: 0;
  }
  
  /* Styling for each option */
  .option {
    margin-bottom: 15px;
  }
  
  /* Styling for the radio input */
  .option-input {
    margin-right: 10px;
  }
  
  /* Styling for the submit button */
  .submit-button {
    padding: 10px 20px;
    margin-top: 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    box-shadow: 2px 2px 2px rgb(195, 195, 195);
  }
  
  .submit-button:hover {
    background-color: #45a049;
  }
  
  /* Styling for the score paragraph */
  .score-paragraph {
    margin-top: 20px;
    font-size: 18px;
    color: #333;
  }
  