
.Login-nav{
        top: 0%;
        display: flex;
        width: 100%;
        left: 0;
        height: 80px;
        color: rgb(249, 189, 99);
        background-color: rgb(0, 153, 255);
        box-shadow: 8px 8px 8px 1px rgb(206, 200, 200);
    
}

.Login-p{
    text-align: center;
    width: 100%;
    padding: 0%;
    margin: 0%;
    margin-left: 6.4%;
}


.Login-h1{
    margin: 0;
    padding: 0;
}
.Login-h3{
    margin: 0;
    padding: 0;
}

.Login-Form{
    align-items: center;
    position: relative;
    top: 80px;
    height: 200px;
    width: 250px;
    background-color:antiquewhite;
    margin: auto;
    border-radius: 6px;
    padding: 25px;
}

.Login-inp1{
    margin-bottom: 10px;
}

.Login-input{
    width: 230px;
    height: 30px;
    border-width: 1.5px;
    border-style:solid;
    border-radius: 4px;
    border-color: black;
}

.Login-label{
    color: rgb(0, 153, 255);
    font-size: large;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.Login-button{
    height: 30px;
    width: 100px;
    background-color: rgb(249, 189, 99);
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-width: 1.5px;
    border-style: solid;
    border-radius: 4px;
}

.Login-btn{
    height: 30px;
    width: 80px;
    margin-top: 1.8%;
    margin-right: 1%;
    background-color: rgb(249, 189, 99);
    border-width: 0;
    border-radius: 4px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: black;
}
