/* Styling for the container div */
.output-audio-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid white;
    border-radius: 7px;
    box-shadow: 2px 2px 2px 2px rgb(205, 205, 205);
    background-color:rgba(250, 247, 242, 0.664);
  }
  
  /* Styling for the progress bar */
  .progress-bar {
    width: 75%;
    height: 20px;
    margin-bottom: 10px;
  }
  
  /* Styling for the select element */
  .rate-select {
    width: 15%;
    padding: 8px;
    margin-bottom: 10px;
    font-size: 16px;
    border: 2px solid black;
    border-radius: 4px;
    font-size: small;
  }
  
  /* Styling for the buttons */
  .button {
    padding: 10px 20px;
    margin-right: 10px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 4px;
    box-shadow: 2px 2px 2px 2px rgb(205, 204, 204);
  }
  
  /* Styling for the button group */
  .button-group {
    margin-top: 20px;
  }
  
  /* Styling for the play button */
  .play-button {
    background-color: #4caf50;
    color: white;
    border: none;
  }
  
  .play-button:hover {
    background-color: #45a049;
  }
  
  /* Styling for the pause button */
  .pause-button {
    background-color: #ff9800;
    color: white;
    border: none;
  }
  
  .pause-button:hover {
    background-color: #e68a00;
  }
  
  /* Styling for the stop button */
  .stop-button {
    background-color: #f44336;
    color: white;
    border: none;
  }
  
  .stop-button:hover {
    background-color: #e53935;
  }
  