/* Home.css */

.Home-nav {
  top: 0%;
  display: flex;
  width: 100%;
  left: 0;
  height: 80px;
  color: rgb(249, 189, 99);
  background-color: rgb(0, 153, 255);
  box-shadow: 8px 8px 8px 1px rgb(206, 200, 200);
}

.Home-p {
  text-align: center;
  width: 100%;
  padding: 0%;
  margin: 0%;
  margin-left: 6.4%;
}

.Home-h1 {
  margin: 0;
  padding: 0;
}

.Home-h3 {
  margin: 0;
  padding: 0;
}

.Home-btn {
  height: 30px;
  width: 80px;
  margin-top: 1.8%;
  margin-right: 1%;
  background-color: rgb(249, 189, 99);
  border-width: 0;
  border-radius: 4px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: black;
}


.floating-box-container {
  position:absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  
}


.floating-box {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  color: #333;
  margin-bottom: 20px;
  animation-duration: 5s; /* Keep animation duration consistent with previous setup */
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.floating-box:hover {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  color: #333;
  margin-bottom: 20px;
  animation-duration: 5s; /* Keep animation duration consistent with previous setup */
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  box-shadow: 5px 5px 5px 5px rgb(198, 198, 198);
}

.floating-box-h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.floating-box-p {
  font-size: 16px;
  line-height: 1.5;
}

/* Ensure the animation properties are not overridden */
@keyframes float-right {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes float-left {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}


/* Apply animation to individual boxes */
.floating-box:nth-child(1) {
  animation-name: float-right;
  background-color: rgb(249, 189, 99);
}

.floating-box:nth-child(2) {
  animation-name: float-left;
  background-color: rgb(0, 153, 255);
}

.floating-box:nth-child(3) {
  animation-name: float-right;
  background-color: white;
}
