.nav {
    top: 0%;
    display: flex;
    width: 100%;
    left: 0;
    height: 80px;
    color: rgb(249, 189, 99);
    background-color: rgb(0, 153, 255);
    box-shadow: 8px 8px 8px 1px rgb(206, 200, 200);
}

.navi{
    text-align: center;
    width: 100%;
    padding: 0%;
    margin: 0%;
    margin-right: 100px;
}

.innerNavi{
    margin: 0px;
    padding: 0px;
}

.innerHeading{
    text-align: center;
    color: rgb(37, 82, 128);
    margin: 0;
    margin-top: 10px;
}

.menu {
    display: flex;
    justify-content: center;
    padding: 10px;
}

.menuItem {
    margin: 0 10px;
    padding: 5px 10px;
    cursor: pointer;
    transition: transform 0.3s ease, background-color 0.3s ease;
    border-radius: 5px; /* Rounded edges */
}

.menuItem.active {
    transform: scale(1.1);
    background-color: #007bff;
    color: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); /* Mild shadow */
}
